.navi{
    position: fixed;
    top: 50px;   
    z-index: 9999999999999999999999;
    left: 50px;
    background: transparent;
    .lnk{
        padding: 10px 20px;
        font-size: 20px;
        display: block;
        background: #fff;
        box-shadow: #171717 3px 3px 3px;
        border-radius: 5px;
        margin-top: 30px;
    }
    a:hover{
        color: #000;
    }
    a:visited{
        color: #000;
    }
    a{
        text-decoration: none;
    }
}