.cf:after{
    content: '';
    display: block;
    clear: both;
}


.Detail{
    padding: 40px;
    margin: 100px 0;
    .Detail_align{
        // float: left;
        display: grid;
        grid-template-columns: 500px auto;
        // grid-template-rows: 200px 200px;
        .detail_img{
            img{
                width: 100%;
            }
        }
        .detail_about{
            margin-left: 100px;
            text-align: center;
            padding-top: 150px;
            p:nth-child(1){
                font-size: 50px;
                margin-bottom: 5px;
            }
            p:nth-child(2){
                font-size: 30px;
                margin-bottom: 50px;
            }
            p:nth-child(3){
                font-size: 30px;
                margin-bottom: 10px;
            }
            p:nth-child(4){
                font-size: 16px;
            }

        }
    }
}